<template>
  <div class="index">
    <div class="index-header-container">
      <div class="index-header clearfix">
        <img :src="logo" alt="logo" class="index-logo">
        <el-menu
          :default-active="activeIndex"
          class="index-menu"
          mode="horizontal"
          @select="handleSelect"
        >
          <el-menu-item index="index">首页</el-menu-item>
          <!-- <el-submenu index="2">
            <template slot="title">我的工作台</template>
            <el-menu-item index="2-1">选项1</el-menu-item>
            <el-menu-item index="2-2">选项2</el-menu-item>
            <el-menu-item index="2-3">选项3</el-menu-item>
          </el-submenu> -->
        </el-menu>
        <div class="container-userinfo" v-show="isLogin">
          <el-dropdown @command="handleCommand">
            <span class="el-dropdown-link">
              <img :src="userImage" alt="avator" class="container-user-image">
            </span>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item command="personal">个人中心</el-dropdown-item>
              <el-dropdown-item command="logout">登出</el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </div>
        <div class="container-loginbox" v-show="!isLogin">
          <span class="container-loginbox-login" @click="login">登录</span>
          <span class="container-loginbox-register" @click="register">注册</span>
        </div>
      </div>
    </div>
    <div class="index-container clearfix">
      <router-view @changeStatus="getChangeEvent"></router-view>
    </div>

    <el-dialog
      :title="dialogTitle"
      :visible.sync="dialogVisible"
      width="300px"
      :close-on-click-modal="false"
    >
      <el-row class="index-register">
        <el-col :span="24">
          <el-input v-model="account" placeholder="请输入账号" size="medium"></el-input>
        </el-col>
        <el-col :span="24">
          <el-input v-model="password" placeholder="请输入密码" size="medium" type="password"></el-input>
        </el-col>
        <el-col :span="24" v-if="dialogType == 'register'">
          <el-input v-model="repassword" placeholder="请输入再输入密码" size="medium" type="password"></el-input>
        </el-col>
        <el-col :span="24">
          <el-button
            type="primary"
            size="medium"
            class="index-register-btn"
            @click="loginInfo"
          >{{btnTitle}}</el-button>
          <div class="index-register-text" @click="turnToPage">{{turnTitle}}</div>
        </el-col>
      </el-row>
      <div class="index-login">
        <span class="index-login-title">第三方登录：</span>
        <img src="https://b-gold-cdn.xitu.io/v3/static/img/github.547dd8a.svg" alt="github">
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { loginUser, registerUser, logout } from "@/api/user";
import { filterObj } from "@/utils/filter";
import { getCookie } from "@/utils/cookies";

export default {
  name: "container",
  data() {
    return {
      dialogTitle: "",
      dialogVisible: false,
      dialogType: "", //login 登录 / register 注册
      btnTitle: "",
      turnTitle: "",
      account: "",
      password: "",
      repassword: "",

      activeIndex: "1",
      logo: require("@/assets/logo.png"),
      userImage: "",
      isLogin: false,
      
      activeIndex: '',
    };
  },
  methods: {
    getChangeEvent() {
      this.getInitData();
    },
    handleCommand(type) {
      if(type == 'personal') {
        this.$router.push('personal')
      } else if( type == 'logout') {
        logout().then(res => {
          if(res.data.ret == 1) {
            this.$message.success('已登出！');
            this.getInitData();
          }
        })
      }
    },
    loginInfo() {
      let data = {
        account: this.account,
        password: this.password
      };

      if (!this.account) {
        this.$message.error("请输入账号!");

        return false;
      }

      if (!this.password) {
        this.$message.error("请输入密码!");

        return false;
      }

      if (this.dialogType == "register") {
        data.repassword = this.repassword;

        if (this.repassword != this.password) {
          this.$message.error("密码不一致");

          return false;
        }

        registerUser(filterObj(data)).then(res => {
          if (res.data.ret == 1) {
            this.$message.success("注册成功");

            this.initData();
            this.dialogVisible = false;
            this.isLogin = true;
            this.getInitData();
          } else if (res.data.ret == 0) {
            this.$message.error(res.data.msg);
          }
        });
      } else if (this.dialogType == "login") {
        loginUser(filterObj(data)).then(res => {
          if (res.data.ret == 1) {
            this.$message.success("登录成功");

            this.initData();
            this.dialogVisible = false;
            this.isLogin = true;
            this.getInitData();
          } else if (res.data.ret == 0) {
            this.$message.error(res.data.msg);
          }
        });
      }
    },
    turnToPage() {
      if (this.dialogType == "register") {
        this.btnTitle = "登录";
        this.turnTitle = "没有账号?注册";
        this.dialogTitle = "登录";
        this.dialogType = "login";
        this.initData();
      } else if (this.dialogType == "login") {
        this.btnTitle = "立即注册";
        this.turnTitle = "已有账号登录";
        this.dialogTitle = "注册";
        this.dialogType = "register";
        this.initData();
      }
    },
    initData() {
      this.account = "";
      this.password = "";
      this.repassword = "";
    },
    login() {
      this.dialogTitle = "登录";
      this.dialogType = "login";
      this.btnTitle = "登录";
      this.turnTitle = "没有账号注册";

      this.dialogVisible = true;
    },
    register() {
      this.dialogTitle = "注册";
      this.btnTitle = "立即注册";
      this.turnTitle = "已有账号登录";
      this.dialogType = "register";

      this.dialogVisible = true;
    },
    handleSelect(key, keyPath) {
      this.$router.push(key);
    },
    getInitData() {
      const userInfo = getCookie("userInfo") ? JSON.parse(decodeURIComponent(getCookie("userInfo"))) : {};

      this.userImage = userInfo.imgId ? "http://localhost:3000/imgUp/file?id=" + userInfo.imgId : require("@/assets/logo.png");

      this.isLogin = userInfo.account ? true : false;
    },
    getNavName() {
      this.activeIndex = this.$route.name;
    }
  },
  mounted() {
    this.getNavName();
    this.getInitData();
  }
};
</script>

